import React from "react";
import { defaultCanvas, defaultPageHistory, pageGenerator, setDefaultActive } from "./common/functions";
import { instruments as instrumentsObj  } from "./common/prototips";
import Canvas from "./components/Canvas";
import Header from "./components/Header";
import Instruments from "./components/Instruments/Instruments";
import Possibilities from "./components/Possibilities/Possibilities";
import utils from "./common/utils";

import * as pdfjs from 'pdfjs-dist/legacy/build/pdf'; 

import "./common/styles/css/coloring.css";

class Coloring extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      pages: pageGenerator(3),
      records: [],
      navigation: true,
      activeCanvasId: 1, canvas: null, ctx: null,
      coordinates: { x: 0, y: 0 },
      rotationDegree: 0,
      pressing: false,
      globalColor: '#000000',
      canvasHover: false,
      cursorPointer: false,
      movingPointer: false,
      resizePointer: false,
      instruments: instrumentsObj,
      importDenied: false,
      dom: {
        successCopy: false,
        successPaste: false,
        copyCancel: false,
        successImport: false,
        successExport: false
      },
      elementsMemory: [],
      selectedElementsMode: false,
      displayedPages: [1, 2, 3],
      pagesNums() {return this.pages.map(page => page.id)}
    }

    this.canvasMethods = {
      canvasResave: this.canvasResave.bind(this),
      coordinatesResave: this.coordinatesResave.bind(this),
      pressingResave: this.pressingResave.bind(this),
      navigationToggle: this.navigationToggle.bind(this),
      pageBackUp: this.pageBackUp.bind(this),
      ctxResave: this.ctxResave.bind(this),
      showCopySuccessMessage: this.showCopySuccessMessage.bind(this),
      pasteSuccessMessage: this.pasteSuccessMessage.bind(this),
      getLastHistory: this.getLastHistory.bind(this),
      copyCancelButtonState: this.copyCancelButtonState.bind(this),
      instrumentSettingsResave: this.instrumentSettingsResave.bind(this),
      elementsMemoryResave: this.elementsMemoryResave.bind(this),
      updateInstruments: this.updateInstruments.bind(this),
      checkFiguresHover: this.checkFiguresHover.bind(this),
      selectedElementsModeResave: this.selectedElementsModeResave.bind(this),
      findSelectedFigure: this.findSelectedFigure.bind(this),
      getLastSelectedFifure: this.getLastSelectedFifure.bind(this),
      returnPreviousAction: this.returnPreviousAction.bind(this),
      checkSelectedFigures: this.checkSelectedFigures.bind(this),
      checkFigures: this.checkFigures.bind(this),
      isSelectedElementsExist: this.isSelectedElementsExist.bind(this)
    }

    this.possibilitieMethods = {
      changePage: this.changePage.bind(this),
      renderBackUpAfterImport: this.renderBackUpAfterImport.bind(this),
      returnPreviousAction: this.returnPreviousAction.bind(this),
      cleanUpPage: this.cleanUpPage.bind(this),
      afterImport: this.afterImport.bind(this),
      navigationToggle: this.navigationToggle.bind(this),
      successfullyExport: this.successfullyExport.bind(this),
      successfullyImport: this.successfullyImport.bind(this),
      createNewPage: this.createNewPage.bind(this),
      pagesResave: this.pagesResave.bind(this),
      displayedPagesResave: this.displayedPagesResave.bind(this),
      importPDF: this.importPDF.bind(this),
      recordsResave: this.recordsResave.bind(this),
      resaveImportDenied: this.resaveImportDenied.bind(this),
    }

    this.headerMethods = {
      resaveRotationDegree: this.resaveRotationDegree.bind(this),
      isSelectedElementsExist: this.isSelectedElementsExist.bind(this),
      returnPreviousAction: this.returnPreviousAction.bind(this),
      getLastSelectedFifure: this.getLastSelectedFifure.bind(this),
      pageBackUp: this.pageBackUp.bind(this),
      clearElementsMemory: this.clearElementsMemory.bind(this),
      getLastHistory: this.getLastHistory.bind(this),
      elementsMemoryResave: this.elementsMemoryResave.bind(this),
    }
  }

  resaveImportDenied(mode) {
    this.setState({ importDenied: mode })
  }

  recordsResave(records) {
    this.setState({ records })
  }

  globalColorResave(globalColor) {
    this.setState({globalColor})
  }

  resaveRotationDegree(rotationDegree) {
    this.setState({ rotationDegree })
  }

  selectedElementsModeResave(mode) {
    this.setState({selectedElementsMode: mode})
  }

  isSelectedElementsExist() {
    return this.state.elementsMemory.some(elem => elem.active)
  }

  checkFiguresHover() {
    this.setState({cursorPointer: this.checkFigures()})
    this.setState({
      movingPointer: this.checkFigures() && this.checkSelectedFigures()
    })
  }

  checkQuater(x, y, x1, y1) {
    return x1 > x ? ( y1 > y ? 4 : 1) : ( y1 > y ? 3 : 2)
  }

  checkFigures() {
    let { coordinates: {x, y}, elementsMemory } = this.state

    for (let i = 0; i < elementsMemory.length; i++) {
      // let { x1, y1, x2, y2 } = elementsMemory[i].coordinates.selectedArea
      let { x1, y1, x2, y2 } = elementsMemory[i].coordinates.rotatedSelectedAreaCoordinates
      let w = 14

      if (elementsMemory[i].canvasId === this.state.activeCanvasId) {
        let quater = this.checkQuater(x1, y1, x2, y2)

        if (quater === 4)
         if ((x1 - w < x && x < x2 + w) && (y1 - w < y && y < y2 + w))
          return true

        if (quater === 3)
          if ((x2 - w < x && x < x1 + w) && (y1 - w < y && y < y2 + w))
           return true

        if (quater === 2)
          if ((x2 - w < x && x < x1 + w) && (y2 - w < y && y < y1 + w))
          return true

        if (quater === 1)
          if ((x1 - w < x && x < x2 + w) && (y2 - w < y && y < y1 + w))
          return true
      }
    }

    return false
  }

  getLastSelectedFifure() {
    let { elementsMemory } = this.state
    return elementsMemory.length && elementsMemory[elementsMemory.length - 1]
  }

  checkSelectedFigures() {
    let { coordinates: {x, y}, elementsMemory } = this.state

    if (elementsMemory.length > 0) {
      for (let i = 0; i < elementsMemory.length; i++) {

        let { x1, y1, x2, y2 } = elementsMemory[i].coordinates.rotatedSelectedAreaCoordinates
        // let { x1, y1, x2, y2 } = elementsMemory[i].coordinates.selectedArea
        let w = 14

        if (elementsMemory[i].canvasId === this.state.activeCanvasId)
          if ((x1 - w < x && x < x2 + w) && (y1 - w < y && y < y2 + w)){
            if (elementsMemory[i].active)
              return true
          }
      }
    }

    return false
  }

  findSelectedFigure() {
    let { coordinates: {x, y}, elementsMemory } = this.state

    for (let i = 0; i < elementsMemory.length; i++) {
      let { x1, y1, x2, y2 } = elementsMemory[i].coordinates.rotatedSelectedAreaCoordinates
      // let { x1, y1, x2, y2 } = elementsMemory[i].coordinates.selectedArea
      let w = 14

      if (elementsMemory[i].canvasId === this.state.activeCanvasId)
        if ((x1 - w < x && x < x2 + w) && (y1 - w < y && y < y2 + w)) {
           // Set active flag
          elementsMemory[i].active = true

          // Change cursor
          this.checkFiguresHover()

          return elementsMemory[i]
        }
    }

    return false
  }

disableSelectedFigure() {
  let { elementsMemory } = this.state
  elementsMemory = elementsMemory.map((item) => {
    item.active = false
    return item
  })

  this.setState({elementsMemory})
}

  canvasHoverResave(e) {
    this.setState({canvasHover: e.target.className.includes('canvas-global')})
  }

  async asyncChangePage(page) {
    return new Promise(resolve => {
      setTimeout(() => {
        this.changePage(page)
        resolve(page)
      }, 500)
    })
  }  
  
  async asyncCreateNewPage(page) {
    return new Promise(resolve => {
      setTimeout(() => {
        this.createNewPage(page)
        resolve(page)
      }, 500)
    })
  }

  async showFistPagesAfterPDFimport(pageNumbers) {
    const pages = pageNumbers.map(n => n + 1).slice(-3)
    this.displayedPagesResave(pages)
  }

  async renderPDFcontent(pdf, pageNumbers, pageNumber) {
    const { canvas } = this.state

    if (pageNumber > pageNumbers.length) 
      return this.showFistPagesAfterPDFimport(pageNumbers)

    if (this.state.pages[pageNumber - 1] === undefined){
      await this.asyncCreateNewPage(pageNumber)
    }      
    else {
      await this.asyncChangePage(pageNumber)
    }

    const page = await pdf.getPage(pageNumber)

    // Support HiDPI-screens
    // let outputScale = window.devicePixelRatio || 1
    // let transform = outputScale !== 1 ? [outputScale, 0, 0, outputScale, 0, 0]: null /* Need to put transform to renderContext */
    
    const settings = {
      rotation: 180,
      dontFlip: true
    }

    const viewport = await page.getViewport({ scale: 1, ...settings })
    const scale = canvas.width / viewport.width

    const scaledViewport = await page.getViewport({ scale, ...settings })

    const renderContext = {
      canvasContext: this.state.ctx,
      viewport: scaledViewport,
    }

    const renderTask = await page.render(renderContext)
    renderTask.promise
    .then(async () => {
      await this.flipCanvasByPage(pageNumber)
    })
    .then(async () => {
      this.pageBackUp(this.state.activeCanvasId)
      await this.renderPDFcontent(pdf, pageNumbers, pageNumber + 1)
    })
  }

  async loadPdf(base64){
    // Load the pdf
    const pdfAsArray = utils.convertDataURIToBinary(base64)
    const loadingTask = await pdfjs.getDocument(pdfAsArray)
    const pdf = await loadingTask.promise

    return pdf
  }

  seqRunner(deeds) {
    return deeds.reduce(function(p, deed) {
      return p.then((function() {
          return deed()
      }))
    }, Promise.resolve())
  }

  generateDefaultEmptyPages() {
    const pages = pageGenerator(3)
    this.pagesResave(pages)
  }

  flipCanvas(pagesNums) {
    const promises = pagesNums.reverse().map(p => () => {
      return new Promise ((resolve) => {
        const pageNum = p + 1
        this.changePage(pageNum)
        
        const { ctx, canvas, canvas: { width } } = this.state
        ctx.translate(width, 0)
        ctx.scale(-1, 1)
        ctx.drawImage(canvas, 0, 0)

        this.pageBackUp(pageNum)
        resolve(pageNum)
      })
    })

    this.seqRunner(promises).then(() => {
      console.log('Success')
    })
  }  
  
  async flipCanvasByPage(pageNum) {
    return new Promise ((resolve) => {
      this.changePage(pageNum)
      
      const { ctx, canvas, canvas: { width } } = this.state
      ctx.translate(width, 0)
      ctx.scale(-1, 1)
      ctx.drawImage(canvas, 0, 0)

      this.pageBackUp(pageNum)
      resolve(pageNum)
    })
  }

  async importPDF(e) {
    pdfjs.GlobalWorkerOptions.workerSrc = window.location.origin + '/pdf.worker.min.js'
    const { files } = e.target

    try {
      if (files && files.length) {

        if (files[0].type !== 'application/pdf') {
          alert('Invalid format!')
          return false
        }

        this.generateDefaultEmptyPages()

        const fileReader = new FileReader()
        fileReader.onload = async () => {
          let base64 = fileReader.result

          const pdf = await this.loadPdf(base64)
          const { numPages = 0 } = pdf

          const pageNumbers = new Array(numPages).fill(1).map((a, i)=> i)

          await this.renderPDFcontent(pdf, pageNumbers, pageNumbers[0] + 1)
        }

        fileReader.readAsDataURL(files[0])
      }

    } catch (e) {
      console.log(e)
    }
  }

  elementsMemoryResave(memoryItem) {
    // let { elementsMemory } = this.state

    // Limit of 5 elements
    // if (elementsMemory.length >= 5) {
    //   elementsMemory.shift()
    // }

    //   this.setState({elementsMemory: [...elementsMemory, memoryItem]}, function () {
    //     this.checkFiguresHover()
    //     this.changeElementsMemoryStatus()
    //     console.log(this.state.elementsMemory)
    //   });

    this.setState({elementsMemory: [memoryItem]}, function () {
      this.checkFiguresHover()
    })
  }

  changeElementsMemoryStatus() {
    let { elementsMemory } = this.state

    // let memory = elementsMemory.map((element, idx) => {
    //   if ((idx + 1) !== elementsMemory.length) {
    //     element.active = false
    //   }

    //   return element
    // })
    if (elementsMemory.length) {
      elementsMemory[0].active = false
    }

    this.setState({elementsMemory})
  }

  displayedPagesResave(displayedPages) {
    this.setState({displayedPages})
  }

  pagesResave(pages) {
    this.setState({pages})
  }

  createNewPage() {
    let pages = Object.assign(this.state.pages)
    let lastPage = pages[pages.length - 1]
    let id = lastPage.id + 1
    let newPage = Object.assign({}, pageGenerator(1)[0], {id})

    pages.push(newPage)
    this.setState({ pages }, () => {
      this.changePage(id)
    })
  }

  afterImport(pages) {
    this.setState({pages: pageGenerator(pages.length + 1), displayedPages: [1, 2, 3]}, () => {
      this.changePage(this.state.pages[this.state.pages.length - 1].id)
        .then(id => this.renderBackUpAfterImport(id))
      
      this.setState({pages}, () => {
        this.setState({ctx: defaultCanvas().getContext('2d')}, () => {
          this.state.ctx.putImageData(pages[0].history[0], 0, 0)

          // Open first page
          new Promise((resolve, reject) => {
            this.changePage(1)
            .then(id => {
              this.renderBackUpAfterImport(id)
              return id
            })
          })
          .then(id => {
            this.pageBackUp(id)
          })
        })
      })
    })
  }

  copyCancel() {
    this.scissorsSettingsResave(Object.assign({}, {
      pasteMode: false,
      x: 0, y: 0, heigth: 0, width: 0,
      previousPicture: null,
      copy: null
    }))

    this.state.ctx.beginPath()
    this.state.ctx.putImageData(this.getLastHistory(), 0, 0)

    this.copyCancelButtonState(false)
    this.pasteSuccessMessage(false)
  }

  ctxResave(ctx) { this.setState({ctx}) }

  scissorsSettingsResave(settings) {
    let instruments = Object.assign(this.state.instruments)
    instruments.scissors.settings = Object.assign(settings)
    this.updateInstruments(instruments)
  }

  instrumentSettingsResave(type, settings) {
    let instruments = Object.assign(this.state.instruments)
    instruments[type].settings = Object.assign(settings)
    this.updateInstruments(instruments)
  }

  showCopySuccessMessage() {
    this.setState({dom: Object.assign(this.state.dom, {successCopy: true})}, () => {
      setTimeout(() => {
        this.setState({dom: Object.assign(this.state.dom, {successCopy: false})})
      }, 2000)
    })
  }

  pasteSuccessMessage(flag) {
    this.setState({dom: Object.assign(this.state.dom, {successPaste: flag})})
  }

  copyCancelButtonState(flag) {
    this.setState({dom: Object.assign(this.state.dom, {copyCancel: flag})})
  }

  successfullyImport() {
    this.setState({dom: Object.assign(this.state.dom, {successImport: true})}, () => {
      setTimeout(() => {
        this.setState({dom: Object.assign(this.state.dom, {successImport: false})})
      }, 2000)
    })
  }

  successfullyExport() {
    this.setState({dom: Object.assign(this.state.dom, {successExport: true})}, () => {
      setTimeout(() => {
        this.setState({dom: Object.assign(this.state.dom, {successExport: false})})
      }, 2000)
    })
  }

  returnPreviousAction() {
    let ctx = this.state.ctx
    let page = this.state.pages.filter(page => page.id === this.state.activeCanvasId)[0]

    if (page.history.length > 1) {
      ctx.beginPath()
      ctx.putImageData(page.history[page.history.length - 2], 0, 0)

      this.setState({ctx}, () => {
        let pages = this.state.pages.map(page => {
          if (page.id === this.state.activeCanvasId) {
            page.history.pop()
          }
          return page
        })

        this.setState({pages})
      })
    }

    // this.removeLastMemoryElement()
  }

  removeLastMemoryElement() {
    let { elementsMemory } = this.state

    if (elementsMemory.length) {
      elementsMemory.pop()
    }
    this.setState({elementsMemory})
  }

  makeCurrentPageCopy(canvasId) {
    let pages = Object.assign(this.state.pages).map(page => {
      if (canvasId === page.id) {
        // Maximum 10 elem to cancel
        if (page.history.length > 10) page.history.shift()

        page.history.push(
          this.state.ctx.getImageData(0, 0, this.state.canvas.width, this.state.canvas.height)
        )
      }
      return page
    })

    return pages
  }

  pageBackUp(canvasId) {
    this.setState({ pages: this.makeCurrentPageCopy(canvasId) })
  }

  componentDidMount() {
    let _this = this
    // DEVELOPMENT MODE
    window.addEventListener('keypress', e => {
      if (e.key === 't' && e.ctrlKey) {
        this.test()
      }

      if (e.key === 'z' && e.ctrlKey) {
        this.returnPreviousAction()
      }

      if (e.key === 'b' && e.ctrlKey) {
        this.pageBackUp(this.state.activeCanvasId)
      }
    })

    window.addEventListener("resize", function(e) {
      _this.state.ctx.putImageData(_this.getLastHistory(), 0, 0)
      let id = _this.state.activeCanvasId
      new Promise((resolve, reject) => {
        _this.changePage(2)
        resolve('1');
      })
      .then(() => {
        _this.changePage(id)
      })
    });


    // Make up page after loading
    window.onload = setTimeout(() => window.scrollTo(0, 0))
  }

  test() {
    // DEVELOPMENT MODE
    const pageNum = this.state.activeCanvasId
    this.changePage(pageNum)
    
    const { ctx, canvas, canvas: { width } } = this.state
    ctx.translate(width, 0)
    ctx.scale(-1, 1)
    ctx.drawImage(canvas, 0, 0)

    this.pageBackUp(pageNum)
  }

  getLastHistory() {
    let page =  this.state.pages.filter(page => page.id === this.state.activeCanvasId)
    return page[0].history[page[0].history.length - 1]
  }

  cleanUpPage() {
    this.state.ctx.beginPath();
    this.state.ctx.clearRect(0, 0, this.state.canvas.width, this.state.canvas.height)

    // Clear all history
    let pages = Object.assign(this.state.pages).map(page => {
      if (page.id === this.state.activeCanvasId) {
        page.history.length = 0
        page.history.push(defaultPageHistory())
      }
      return page
    })

    this.setState({pages})
  }

  checkPageDirty(id) {
    this.state.pages.forEach(page => {
      if (page.id === id && page.temp) {
        this.state.ctx.putImageData(page.temp, 0, 0)
      }
    })
  }

  navigationToggle() {
    this.setState({navigation: !this.state.navigation}, () => {
      this.state.ctx.putImageData(this.getLastHistory(), 0, 0)
    })
  }

  openNavigation() {
    this.setState({navigation: true})
  }

  canvasResave(canvas, ctx, activeCanvasId) {
    this.setState({canvas, ctx, activeCanvasId}, () => {
      this.checkPageDirty(activeCanvasId)
    })
  }

  coordinatesResave(coordinates) {
    this.setState({coordinates})
  }

  pressingResave(flag) {
    this.setState({pressing: flag})
  }

  updateInstruments(obj) {
    this.setState({instruments: Object.assign(instrumentsObj, obj)})
  }

  savePreviousPageData(id) {
    let pages = (Object.assign(this.state.pages)).map(page => {
      if (page.id === id) {
        page.temp = this.state.ctx.getImageData(0, 0,
          document.getElementById(`canvas${id}`).width,
          document.getElementById(`canvas${id}`).height
        )
      }
      return page
    })

    this.setState({ pages })
  }

  renderBackUpAfterImport(id) {
    this.pageBackUp(id)
    let pages = (Object.assign(this.state.pages)).map(page => {
      if (page.id === id) {
        if (page?.import) {
          delete page.import
        }
      }
      return page
    })

    this.setState({pages})
  }

  async changePage(id) {
    return new Promise((resolve, reject) => {
      this.savePreviousPageData(this.state.activeCanvasId)

      this.setState({activeCanvasId: id}, (state) => {
        let canvas = document.getElementById(`canvas${id}`)
        this.canvasResave(canvas, canvas?.getContext('2d'), id)
      })

      resolve(id)
    })
  }

  clearElementsMemory() {
    this.setState({elementsMemory: []})
  }

  coloringAppClickDown(e) {
    let { instruments } = this.state

    if (instruments.glove.active) {
      if (
        e.target.tagName !== 'CANVAS' && 
        !e.target.className.includes('special-resize-block')
      ) {
        // Disselect glove
        let _instruments = setDefaultActive(Object.assign(instruments), 'glove')
        _instruments.glove.active = false
        this.updateInstruments(_instruments)

        // Remove selecting border if selected elements exist
        if (this.isSelectedElementsExist()) {
          this.returnPreviousAction()
          this.pageBackUp()
          this.clearElementsMemory()
          this.resaveRotationDegree(0)
        }
      }
    }

    this.checkFiguresHover()
  }

  render() {
    return (
      <div
        className="coloring-app"
        onMouseDown={(e) => this.coloringAppClickDown(e)}
        onMouseMove={(e) => this.canvasHoverResave(e)}>
        {this.state.navigation ?
          <Header
            globalColorResave={this.globalColorResave.bind(this)}
            updateInstruments={this.updateInstruments.bind(this)}
            methods={this.headerMethods}
            copyCancel={this.copyCancel.bind(this)}
            state={this.state}/> : ''}
        <div className="work-space">
          {this.state.navigation ?
            <Instruments
            updateInstruments={this.updateInstruments.bind(this)}
            copyCancel={this.copyCancel.bind(this)}
            state={this.state}/> : ''}
          <Canvas
            methods={this.canvasMethods}
            parentState={this.state}/>
          {this.state.navigation ?
            <Possibilities
            methods={this.possibilitieMethods}
            parentState={this.state}/> : ''}
        </div>
      </div>
    );
  }
}

export default Coloring
